import React, { Component } from 'react';

class Formulari extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            dia: '',
            hora: '',
            comensals: '',
            observacions: ''
        }
        this.updateStateDia = this.updateStateDia.bind(this);
        this.updateStateHora = this.updateStateHora.bind(this);
        this.updateStateComensals = this.updateStateComensals.bind(this);
        this.updateStateObservacions = this.updateStateObservacions.bind(this);
    };

    updateStateDia(e) {
        this.setState({ dia: e.target.value });
    }

    updateStateHora(e) {
        this.setState({ hora: e.target.value });
    }

    updateStateComensals(e) {
        this.setState({ comensals: e.target.value });
    }

    updateStateObservacions(e) {
        this.setState({ observacions: e.target.value });
    }

    render() {
        return (
            <div>
                <div>
                    <div className="container">
                        <h1 className="text-center">Reserva nom restaurant</h1>
                        <div className="row justify-content-center mt-5">
                            <div className="col-md-10">
                                <div className="row">
                                    <div className="form-inline">
                                        <div className="form-group col-md-4">
                                            <label htmlFor="dia">Dia:</label>
                                            <input type="date" className="ml-2" value={this.state.dia} onChange={this.updateStateDia} />
                                        </div>
                                        <div className="form-group col-md-4">
                                            <label htmlFor="hora">Hora:</label>
                                            <input type="time" className="ml-2" value={this.state.hora} onChange={this.updateStateHora} />
                                        </div>
                                        <div className="form-group col-md-4">
                                            <label htmlFor="comensals">Nre. comensals:</label>
                                            <input type="number" className="ml-2" style={{ width: "25%" }} value={this.state.comensals} onChange={this.updateStateComensals} />
                                        </div>
                                    </div>
                                    <div className="form-group col-md-10">
                                        <label htmlFor="observacions">Observacions:</label>
                                        <input type="textarea" className="form-control" onChange={this.updateStateObservacions} />
                                    </div>
                                    <div className="form-group col-md-10">
                                        <button type="button" className="btn btn-primary float-right" onClick={this.insertReserva}>Enviar</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <h4>{this.state.dia} {this.state.hora} {this.state.comensals} {this.state.observacions}</h4>
                    </div>
                </div >
            </div >
        );
    }
}

export default Formulari;